import React from "react";
import { Header, Label, Card, List, Button, Divider } from "semantic-ui-react";
import profile from "./profile";

const PositionTimeLineItem = props => (
  <div className="container">
    <Card fluid>
      <Card.Content>
        <Card.Header content={props.position.title} />
        <Card.Meta content={props.position.company_name} />
      </Card.Content>
      <Card.Content extra>
        <strong>From:</strong> {props.position.started_on}
        <strong> To:</strong> {props.position.finished_on || "Actual"}
      </Card.Content>
    </Card>
  </div>
);

const PositionSection = props => {
  return (
    <div className="divider-space">
      <Header size="medium">Positions</Header>
      <Divider />
      <div className="timeline">
        {props.positions.map(position => (
          <PositionTimeLineItem key={position.started_on} position={position} />
        ))}
      </div>
    </div>
  );
};

const SkillSection = props => (
  <div className="divider-space">
    <Header size="medium">Skills</Header>
    <Divider />
    <Label.Group color="blue">
      {props.skills.map(skill => <Label key={skill.name}>{skill.name}</Label>)}
    </Label.Group>
  </div>
);

const PublicationItem = props => (
  <List.Item>
    <List.Icon name="file text" size="large" verticalAlign="middle" />
    <List.Content>
      <List.Header>
        <a target="_blank" href={props.publication.url}>
          {props.publication.name}
        </a>
      </List.Header>
      <List.Description>{props.publication.publication_date}</List.Description>
    </List.Content>
  </List.Item>
);

class PublicationSection extends React.Component {
  state = {
    showMore: false
  };

  get publicationsToDisplay() {
    var publications = this.props.publications.sort((a, b) => {
      let aPublicationDate = new Date(a.publication_date);
      let bPublicationDate = new Date(b.publication_date);

      if (aPublicationDate < bPublicationDate) return 1;
      if (aPublicationDate > bPublicationDate) return -1;
      return 0;
    });

    if (this.state.showMore) return publications;
    return publications.slice(0, 5);
  }

  get displayButton() {
    if (this.state.showMore)
      return (
        <Button negative onClick={() => this.setState({ showMore: false })}>
          Show less
        </Button>
      );
    return (
      <Button positive onClick={() => this.setState({ showMore: true })}>
        Show more
      </Button>
    );
  }

  render() {
    return (
      <div className="divider-space">
        <Header size="medium">Publications</Header>
        <Divider />
        <List divided relaxed>
          {this.publicationsToDisplay.map(publication => (
            <PublicationItem key={publication.url} publication={publication} />
          ))}
        </List>
        {this.displayButton}
      </div>
    );
  }
}

const SummarySection = props => (
  <div className="divider-space">
    <Header>Summary</Header>
    <Divider />
    <p>{props.profile.summary}</p>
  </div>
);

const EducationSection = props => (
  <div className="divider-space">
    <Header>Education</Header>
    <Divider />
    <Card.Group>
      {props.education.map(course => (
        <Card key={course.degree_name}>
          <Card.Content>
            <Card.Header>{course.degree_name}</Card.Header>
            <Card.Meta>{course.school_name}</Card.Meta>
          </Card.Content>
          <Card.Content extra>
            From: {course.start_date} To: {course.end_date}
          </Card.Content>
        </Card>
      ))}
    </Card.Group>
  </div>
);

export default class Linkedin extends React.Component {
  state = {
    profile
  };

  render() {
    return (
      <div>
        <SummarySection profile={this.state.profile.profile} />
        <SkillSection skills={this.state.profile.skills} />
        <PositionSection positions={this.state.profile.positions} />
        <EducationSection education={this.state.profile.education} />
        <PublicationSection publications={this.state.profile.publications} />
      </div>
    );
  }
}



