import React, { Component } from "react";
import { Header, Image, Segment, Card, Tab } from "semantic-ui-react";
import RepositoryCard from "./github/card";
import GithubRepository from "./github/repository";
import WordPress from "./wordpress";
import Linkedin from "./linkedin";

let username = "rodrigosaint";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      repositoryCollection: [],
      user: {}
    };
    GithubRepository.getUser(username).then(({ data }) =>
      this.setState({ user: data })
    );

    GithubRepository.getRepository(username)
      .then(({ data }) =>
        data.filter(
          repository =>
            !repository.fork && `${username}.github.io` !== repository.name
        )
      )
      .then(repositoryCollection => this.setState({ repositoryCollection }));
  }

  render() {
    if (!this.state.user) return null;
    const panes = [
      {
        menuItem: {
          key: "Linkedin",
          content: "Linkedin",
          icon: "linkedin square"
        },
        render: () => (
          <Tab.Pane>
            <Linkedin />
          </Tab.Pane>
        )
      },
      {
        menuItem: {
          key: "Github",
          content: "Github",
          icon: "github square"
        },
        render: () => (
          <Tab.Pane>
            <Card.Group>
              {this.state.repositoryCollection.map(repository => (
                <RepositoryCard
                  key={repository.html_url}
                  repository={repository}
                />
              ))}
            </Card.Group>
          </Tab.Pane>
        )
      },
      {
        menuItem: {
          key: "Blog",
          content: "Blog",
          icon: "edit"
        },
        render: () => (
          <Tab.Pane>
            <WordPress />
          </Tab.Pane>
        )
      }
    ];
    return (
      <Segment>
        <Header as="h2">
          <Image circular src={this.state.user.avatar_url} />
          {this.state.user.name} - Resume
        </Header>
        <Tab menu={{ secondary: true, pointing: true }} panes={panes} />
      </Segment>
    );
  }
}

export default App;



